.footer {
  background-image: url('../img/footer-bg.png');
  background-repeat: no-repeat;
  background-color: $brown-grey;
  background-size: contain;
  background-position: 100% 0;
  padding-top: 35px;
  @include r(767) {
    background-image: none; }
  &__logo {
    @include r(1024) {
      @include center; }
    img {
      width: 125px; } }
  &__social {
    padding-top: 25px;
    ul {
      display: flex;
      @include r(1024) {
        justify-content: center;
        margin-bottom: 40px; } }
    li {
      padding-right: 20px; }
    a {
      width: 40px;
      height: 40px;
      background: $beige;
      border: 2px solid $brown-grey;
      box-shadow: 0px 0px 0px 2px $beige;
      @include center;
      border-radius: 100%;
      @include tr;
      i {
        color: $brownish-grey-two; }
      &:hover {
        box-shadow: 0px 5px 10px 0 rgba(0, 0, 0, 0.29);
        border: 4px solid $beige; } } }
  &__nav {
    @include r(767) {
      @include center;
      ul {
        @include center-column; } }
    li {
      a {
        font-size: 14px;
        line-height: 2.14;
        letter-spacing: 0.7px;
        color: $white;
        text-transform: uppercase;
        @include r(767) {
          display: flex;
          justify-content: center;
          text-align: center; } } } }
  &__info-user {
    @include r(767) {
      @include center;
      margin-top: 40px;
      ul {
        @include center-column; } }
    li a {
      font-size: 14px;
      font-weight: 200;
      line-height: 2.57;
      letter-spacing: 0.7px;
      color: $white;
      text-decoration: underline;
      @include r(767) {
        display: flex;
        justify-content: center;
        text-align: center; } } }
  &__created {
    padding: 20px 0 30px;
    margin-top: 40px;
    border-top: 1px solid $beige;
    p {
      font-size: 11px;
      font-weight: 200;
      line-height: 1.37;
      text-align: center;
      color: $white; } } }
