html {}
body {
  font-family: 'IBM Plex Serif', serif;
  font-size: 18px;
  box-sizing: border-box;
  position: relative; }
.container {
  width: 100% !important;
  max-width: 1165px;
  box-sizing: border-box;
  @include r(1170) {
    padding: 0 15px; } }
a {
  text-decoration: none;
  transition: all 0.3s; }
h1, h2, h3, h4 {
  font-weight: normal; }
.heading {
  padding: 80px 0 45px;
  h2 {
    font-size: 48px;
    line-height: 1;
    letter-spacing: 0.5px;
    text-align: center;
    text-shadow: 0px 3px 7px rgba(0, 0, 0, 0.5);
    @include r(767) {
      font-size: 32px; } }
  &-primary {
    h2 {
      color: $black; } }
  &-secondary {
    h2 {
      color: $white; } } }
@include r(767) {
	.hidden-xs {
		display: none !important; } }
@media only screen and (min-width: 768px) and (max-width: 1024px) {
	.hidden-sm {
		display: none !important; } }
@include rmin(1024) {
	.hidden-md {
		display: none !important; } }


.btn {
  height: 67px;
  @include center;
  display: inline-flex;
  padding: 0 40px;
  border-radius: 6px;
  text-transform: uppercase;
  letter-spacing: 1.4px;
  font-size: 18px;
  @include tr;
  @include r(767) {
    font-size: 16px; }
  &-primary {
    background-color: $lipstick-red;
    color: $white;
    &:hover {
      box-shadow: 0px 7px 21px 0 rgba(0, 0, 0, 0.76); } }
  &-secondary {
    background: $white;
    color: $lipstick-red;
    letter-spacing: 0.1px;
    padding: 0 20px;
    height: 48px;
    text-transform: none;
    &:hover {
      box-shadow: 0px 3px 16px 0 rgba(0, 0, 0, 0.3); } } }
.swiper-container {
  width: 100%;
  height: 100%; }
.swiper-button-prev, .swiper-button-next {
  background: none !important;
  i {
    font-size: 36px;
    color: $white; } }
