.treatment {
  &__intro {
    padding-top: 240px;
    background: url('../img/treatment-bg.png') no-repeat;
    background-repeat: no-repeat;
    background-position: 50% 50%;
    background-size: cover;
    h1 {
      font-size: 48px;
      line-height: 1.15;
      letter-spacing: 0.5px;
      text-align: center;
      color: $white;
      margin-bottom: 45px; }
    a {
      @include center;
      padding-bottom: 8px;
      padding-right: 4px;
      i {
        font-size: 36px;
        color: $white; } } }
  &__content {
    padding: 50px 0;
    h3 {
      font-size: 24px;
      line-height: 1.25;
      letter-spacing: 0.2px;
      text-align: center;
      color: $black;
      max-width: 967px;
      @include center-relative-x;
      position: relative;
      margin-bottom: 45px; }
    h2 {
      font-size: 30px;
      line-height: 1.2;
      letter-spacing: 0.3px;
      text-align: center;
      color: $black;
      max-width: 775px;
      @include center-relative-x;
      position: relative;
      margin-bottom: 40px; } }
  &__block {
    background-color: $beige;
    padding: 35px 45px;
    @include center-column;
    width: calc(100% - 10px);
    margin-bottom: 30px;
    min-height: 430px;
    justify-content: space-between;
    img {
      margin-bottom: 15px; }
    h4 {
      font-size: 24px;
      font-weight: 300;
      line-height: 1;
      letter-spacing: 0.2px;
      text-align: center;
      color: $black;
      text-shadow: 0px 3px 7px rgba(0, 0, 0, 0.5);
      margin-bottom: 10px; }
    p {
      font-size: 18px;
      line-height: 1.33;
      letter-spacing: -0.2px;
      text-align: center;
      color: $black;
      margin-bottom: 10px; } } }
